import React from "react"

import Layout from "../components/Layout"
import Meta from "../components/Meta"
import PageHeader from "../components/PageHeader"

const IndexPage = () => {
    return (
        <Layout>
            <Meta title="Home" />
            <PageHeader
                title="William Neild"
                subtitle="An aspriring software developer"
                typewriter={true}
                alternatives={["A student who loves technology", "A future blogging star"]}
                big={true}
                buttons={[
                    ["Blog", "/blog"],
                    ["Projects", "/projects"],
                ]}
            />
        </Layout>
    )
}

export default IndexPage
